<template>
	<div class="personal-content-body">
		<div class="table-content">
			<div class="clear table-content-title">
				<!-- <div class="left">假期管理</div> -->
				<el-input placeholder="搜索" v-model="searchText" class="header-right-time right" :clearable='true' @change="initData()">
					<i slot="prefix" class="el-input__icon el-icon-search "></i>
				</el-input>
			</div>
			<div class="table-group">
				<div class="table-group-content">
					<!-- pe数据列表 -->
					<div class="table">
						<el-table :data="tableData" border style="width: 100%">
							<el-table-column fixed="left" label="姓名" width="100">
								<template slot-scope="scope">
									{{scope.row.name}}
								</template>
							</el-table-column>
							<el-table-column fit min-width='220px' v-for="(item,index) in tabelHeader" :index="index" :key="index" :label="'剩余'+item.leave_name+'(小时)'">
								<template slot-scope="scope">
									<span v-if="scope.row.data[item.leave_type]!=undefined">{{scope.row.data[item.leave_type]}}</span>
									<span v-else>0</span>
								</template>
							</el-table-column>
							<el-table-column fixed="right" label="操作" width="100">
								<template slot-scope="scope">
									<el-button class="el_button" @click="handleEditClick(scope.row)" type="text" size="small">编辑</el-button>
								</template>
							</el-table-column>
						</el-table>
						<div class="pagination-normal" v-if="total!=0">
							<el-pagination v-if="total" @size-change="everyPageSizeChange" prev-text="上一页" next-text="下一页" background @current-change="currentChange" :total="total" :current-page="currentPage" :page-size="pageSize"  layout="sizes, prev, pager, next, jumper" >
    						</el-pagination>
							<!-- <el-pagination background layout="prev, pager, next" :total="total" :page-size="pageSize" prev-text="上一页" next-text="下一页" @current-change="currentChange" :current-page="currentPage">
							</el-pagination> -->
						</div>
					</div>
				</div>
			</div>
		</div>
		<!-- 添加员工弹窗 -->
		<el-dialog :title="dialogTitle" :visible.sync="visibleDialog" width="570px" center :append-to-body='true'>
			<div class="dialog_content">
				<div class="vacation_list">
					<div class="vacation_item" v-for="(item,index) in dialogData.data">
						<div class="vacation_item_left">{{item.leave_name}}</div>
						<div class="vacation_item_center">
							<input v-model="item.hours" @keyup="inputTime($event,item.hours,index)">
						</div>
						<div class="vacation_item_right">小时</div>
					</div>
				</div>
			</div>
			<div slot="footer" class="dialog_footer clear">
				<button class="dialog-btn-submit btn-blue marginl_10" @click="submitEdit">确定</button>
				<button class="dialog-btn-submit btn-blue-border" @click="cancleEditor">取消</button>
			</div>
		</el-dialog>
	</div>
</template>
<script>
	import { userVacationList, addVacationTime } from '../../config/api'
	//import fundLead from '../../pages/fundrunComponent/fundLead.vue'
	import {
		dialogConfirm,
		dialogConfirmHasbtn,
		compareJson
	} from '../../config/mUtils'
	export default {
		data() {
			return {
				tabelHeader: [],
				tableData: [],
				currentPage: 1,
				pageSize: 10,
				searchText: '',
				total: 0,
				visibleDialog: false,
				dialogTitle: '假期修改',
				dialogData: '',

				needSave: false,
			}
		},
		components: {
			//fundLead
		},
		created() {
			this.initData();
		},
		computed: {

		},
		props: ['fundId'],
		mounted() {

		},
		methods: {
			initData() {
				this.currentPage = 1;
				this.getVocation();
			},
			currentChange(val) {
				if(this.total > this.pageSize) {
					this.currentPage = val;
					this.getVocation();
				}
			},
			//表格数据
			getVocation() {
				userVacationList({
					page: this.currentPage,
					number: this.pageSize,
					searchText: this.searchText
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.tableData = res.data.payload.data
						this.tabelHeader = res.data.payload.leave
						this.total = res.data.payload.total
					}
				});
			},
			inputTime(e, hours, index) {
				// console.log(1111);
				this.needSave = true;
				let code = e.keyCode;
				if(!((code >= 48 && code <= 57) || code == 8 || code == 46 || code == 190)) {
					this.dialogData.data[index].hours = hours.substring(0, hours.length - 1);
				}
			},
			//编辑
			handleEditClick(row) {
				this.needSave = false;
				this.dialogData = JSON.parse(JSON.stringify(row));
				this.dialogTitle = row.name + '的假期修改';
				this.visibleDialog = true;
				this.dialogData.data = [];
				this.tabelHeader.forEach(item => {
					let dataitem = {};
					dataitem.leave_type = item.leave_type;
					dataitem.leave_name = item.leave_name;
					if(row.data[item.leave_type] == undefined) {
						dataitem.hours = 0;
					} else {
						dataitem.hours = row.data[item.leave_type];
					}
					this.dialogData.data.push(dataitem);
				})
			},
			submitEdit() {
				let data = this.dialogData.data;
				data.forEach(item => {
					item.hours = parseFloat(item.hours);
				})
				addVacationTime({
					user_id: this.dialogData.user_id,
					data: JSON.stringify(data)
				}).then(res => {
					if(res.data.msgNo == 1) {
						dialogConfirm('success', '修改成功！');
						this.visibleDialog = false;
						this.initData();
					} else {
						dialogConfirm('fail', res.data.message)
					}
				})
			},
			cancleEditor() {
				if(this.needSave) {
					dialogConfirmHasbtn('确定放弃修改吗？').then(() => {
						this.visibleDialog = false;
					}).catch(() => {});
				} else {
					this.visibleDialog = false;
				}
			},
			//修改页面条数时调用pageSize
			everyPageSizeChange(e){
				this.pageSize = e;
				this.getVocation()
			},
		}
	}
</script>
<style lang="scss" scope>
	@import '../../style/modal.scss';
	.table-content {
		.table-content-title {
			margin-bottom: 16px;
		}
	}
	.table{
		padding: 10px 0;
	}
	.vacation_list {
		display: flex;
		flex-wrap: wrap;
		.vacation_item {
			display: flex;
			flex-shrink: 0;
			width: 220px;
			height: 50px;
			border: 1px solid rgba(245, 245, 245, 1);
			border-left: 6px solid #A2C0E3;
			align-items: center;
			flex-grow: 0;
			overflow: hidden;
			margin-right: 20px;
			margin-bottom: 10px;
			&:nth-child(2n) {
				margin-right: 0px;
			}
			&:nth-child(1) {
				border-left-color: #A2C0E3
			}
			&:nth-child(2) {
				border-left-color: #E8E2B7
			}
			&:nth-child(3) {
				border-left-color: #AED4AB
			}
			&:nth-child(4) {
				border-left-color: #BDE0EA
			}
			&:nth-child(5) {
				border-left-color: #E4C2C2
			}
			&:nth-child(6) {
				border-left-color: #C5C1EA
			}
			&:nth-child(7) {
				border-left-color: #DFBFE6
			}
			&:nth-child(8) {
				border-left-color: #EACEC0
			}
		}
		.vacation_item_left {
			padding: 0px 15px;
			height: 25px;
			border-right: 1px solid #F1F1F1;
			flex-shrink: 0;
			display: flex;
			font-size: 14px;
			color: #282828
		}
		.vacation_item_center {
			flex-shrink: 1;
			display: flex;
			padding: 0px 0px 0px 15px;
			input {
				width: 100%;
				height: 24px;
				line-height: 24px;
				border-radius: 3px;
				padding: 0px 5px;
				box-sizing: border-box;
				&:focus {
					box-shadow: 0px 0px 2px 0px rgba(60, 152, 232, 1);
					border: 1px solid rgba(60, 152, 232, 1);
				}
			}
		}
		.vacation_item_right {
			padding: 0px 15px;
			font-size: 14px;
			color: #808080;
			flex-shrink: 0;
			display: flex;
		}
	}
	
	.el_button {
		span {
			color: #3C98E8
		}
	}
	
	/*新加样式*/
	// .table-content{
	// 	padding: 20px 30px 30px 30px;
	// }
	// .table-group-tablebox{
	// 	padding: 10px 0;
	// }
</style>


